// Import required component dependencies.
@import (reference) '@cfpb/cfpb-grid/src/cfpb-grid.less';

//
// Banner
// Global banner in the header.
//

.o-banner {
  padding: unit((@grid_gutter-width / 2 / @base-font-size-px), em) 0;
  background: @gold-10;
  border-bottom: 1px solid @gray-40;
  font-size: 0.875em;

  // Override notification box styling to align notification in header.
  .m-notification {
    border: none;
    padding: 0;

    &_icon {
      left: 0;
      top: 0;
    }
  }

  &__dark {
    background: @teal-mid-dark;
    border-color: @teal-mid-dark;
    color: @white;

    a {
      border-color: @white;
      color: @white;

      &:hover {
        border-color: @gray-15;
        color: @gray-15;
      }
    }

    .m-notification {
      background: @teal-mid-dark;

      .cf-icon-svg {
        fill: @white;
      }
    }
  }

  .respond-to-min( @bp-sm-min, {
    font-size: 1em;
  } );
}
