/* ==========================================================================
   Design System
   Utilities
   ========================================================================== */

//
// JS-only
//

.u-js-only {
  .no-js & {
    display: none !important;
  }
}

//
// To hide an element when JS is on
// And show it when JS is off
//

.u-hide-if-js {
  display: none !important;
  .no-js & {
    display: block !important;
  }
}

//
// Clearfix
//

.u-clearfix {
  &::after {
    content: '';
    display: table;
    clear: both;
  }
}

//
// Visually hidden
//

.u-visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  // `clip` is deprecated, but retained for safety in making sure that this
  // utility works as expected for screenreaders. Comma-separated syntax is
  // not used because space-separated is more backward-compatible,
  // per https://developer.mozilla.org/en-US/docs/Web/CSS/clip
  clip: rect(0 0 0 0);
}

//
// Width-specific display
//

.u-hide-on-mobile {
  .respond-to-max( @bp-xs-max, {
    display: none;
  } );
}

.u-show-on-mobile {
  display: none;

  .respond-to-max( @bp-xs-max, {
    display: block;
  } );
}

//
// Hide an element.
//

.u-hidden {
  display: none !important;
}

//
// Hide an element while retaining its layout.
//

.u-invisible {
  visibility: hidden;
}

// TODO: Deprecated. Remove in CFv5.
//
// Inline block
//

.u-inline-block {
  display: inline-block;
}

//
// Floating right
//

.u-right {
  float: right;
}

//
// Break word
//

.u-break-word {
  word-break: break-all;
}

//
// Don't break a word across a line break.
//

.u-nowrap {
  white-space: nowrap;
}

//
// Align with button
//

.u-align-with-btn( @font-size: @base-font-size-px ) {
  display: inline-block;
  line-height: normal;
  vertical-align: middle;
}

//
// Flexible proportional containers
//

.u-flexible-container-mixin( @width: 16, @height: 9 ) {
  @ratio: (@height / @width) * 100;

  position: relative;
  padding-bottom: ~'@{ratio}%';
  height: 0;
}

.u-flexible-container {
  .u-flexible-container-mixin();

  &_inner {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__4-3 {
    .u-flexible-container-mixin( 4, 3 );
  }
}

//
// Link mixins
//

.u-link__colors() {
  .u-link__colors-base();
}

.u-link__colors( @c ) {
  .u-link__colors-base(
    @c, @c, @c, @c, @c,
    @c, @c, @c, @c, @c
  );
}

.u-link__colors( @c, @h ) {
  .u-link__colors-base(
    @c, @c, @h, @h, @c,
    @c, @c, @h, @h, @c
  );
}

.u-link__colors( @c, @v, @h, @f, @a ) {
  .u-link__colors-base(
    @c, @v, @h, @f, @a,
    @c, @v, @h, @f, @a
  );
}

.u-link__colors( @c, @v, @h, @f, @a, @bc, @bv, @bh, @bf, @ba ) {
  .u-link__colors-base(
    @c, @v, @h, @f, @a,
    @bc, @bv, @bh, @bf, @ba
  );
}

.u-link__colors-base(
@c:  @link-text,
@v:  @link-text-visited,
@h:  @link-text-hover,
@f:  @link-text,
@a:  @link-text-active,
@bc: @link-underline,
@bv: @link-underline-visited,
@bh: @link-underline-hover,
@bf: @link-underline,
@ba: @link-underline-active ) {
  color: @c;
  border-color: @bc;

  &:visited,
  &.visited {
    border-color: @bv;
    color: @v;
  }

  &:hover,
  &.hover {
    border-color: @bh;
    color: @h;
  }

  &:focus,
  &.focus {
    border-color: @bf;
    color: @f;
  }

  &:active,
  &.active {
    border-color: @ba;
    color: @a;
  }
}

.u-link__border() {
  border-bottom-width: 1px;
}

.u-link__no-border() {
  border-bottom-width: 0 !important;
}

.u-link__hover-border() {
  border-bottom-width: 0 !important;

  &:hover,
  &.hover,
  &:focus,
  &.focus {
    border-bottom-width: 1px !important;
  }
}

